:root {
  --accent-color: #8a3c4c;
  --text-color: #cecece;
  --shadow: drop-shadow(0px 4px 4px rgb(0, 0, 0));
  --circle-size: clamp(1.6rem, 2dvh + 0.5rem, 2.5rem);
  --title-size: clamp(1.4rem, 1.2vw + 0.8rem, 2rem);
  --years-size: clamp(1rem, 0.8vw + 0.6rem, 1.4rem);
  --desc-size: clamp(0.9rem, 0.6vw + 0.5rem, 1.2rem);
  --gap-size: clamp(0.3rem, 0.8dvh, 0.8rem);
  --margin-bottom: clamp(1rem, 3dvh, 2rem);
}

.ExperienceContainer {
  position: relative;
  display: flex;
  flex-direction: row;
  gap: clamp(1rem, 2.5vw, 2.2rem);
  height: 60%;
  margin-left: clamp(0.5rem, 1vw, 1.5rem);
}

.VerticalLine {
  height: 100%;
  border-right: 2px solid var(--accent-color);
  filter: var(--shadow);
}

.Circle {
  position: absolute;
  margin-left: calc(var(--circle-size) / -2);
  height: var(--circle-size);
  width: var(--circle-size);
  border-radius: 50%;
  background-color: var(--accent-color);
}

.ExperienceInfo {
  display: flex;
  flex-direction: column;
  gap: var(--gap-size);
}

.PositionCompany {
  margin: 0;
  color: var(--accent-color);
  font-weight: 500;
  font-size: var(--title-size);
  font-family: 'Poppins', sans-serif;
}

.Years {
  margin: 0;
  color: var(--text-color);
  font-size: var(--years-size);
  font-weight: 500;
  font-family: 'Poppins', sans-serif;
}

.Description {
  margin: 0 0 var(--margin-bottom) 0;
  color: var(--text-color);
  font-size: var(--desc-size);
  font-family: 'Poppins', sans-serif;
  line-height: 1.5;
}
