:root {
  --accent-color: #8a3c4c;
  --text-color: #cecece;
  --shadow: drop-shadow(0px 7px 7px rgb(0, 0, 0));
  --card-radius: 30px;
  --gap-size: clamp(0.8rem, 1.2vw, 1.5rem);
  --card-height: clamp(12rem, 25dvh, 18rem);
  --font-size-base: clamp(0.9rem, 0.7vw + 0.6rem, 1.2rem);
  --title-size: clamp(1.4rem, 1.8vw + 0.5rem, 2.2rem);
  --tag-gap: clamp(0.4rem, 1vw, 0.8rem);
}

.ProjectCard {
  width: 100%;
  height: var(--card-height);
  background-color: transparent;
  border: 1.5px solid var(--accent-color);
  filter: var(--shadow);
  border-radius: var(--card-radius);
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  cursor: pointer;
  transition: transform 0.3s ease;
}

.ProjectCard:hover {
  transform: scale(1.02);
}

/* üñºÔ∏è Obrazek */
.projectImage {
  width: 40%;
  height: 85%;
  margin: 2.5%;
  border: 1.5px solid var(--accent-color);
  border-radius: 20px;
  object-fit: cover;
  background-color: transparent;
}

/* üìÑ Zawarto≈õƒá projektu */
.Project {
  flex: 1;
  display: flex;
  flex-direction: column;
  gap: var(--gap-size);
  text-align: left;
  color: var(--text-color);
  font-size: var(--font-size-base);
  margin: 2.5% 2% 2.5% 2%;
  line-height: 1.5;
  overflow: hidden;
}

.Title {
  font-size: var(--title-size);
  font-weight: 500;
  color: var(--accent-color);
}

/* üè∑Ô∏è Tag container */
.TagsContainer {
  display: flex;
  flex-wrap: wrap;
  gap: var(--tag-gap);
}

.ProjectDescription {
  flex: 1;
  overflow-y: auto;
  margin-right: 1rem;
  padding-right: 0.3rem;
  font-size: var(--font-size-base);
  scrollbar-width: thin;
  scrollbar-color: var(--accent-color) transparent;
}

.ProjectDescription::-webkit-scrollbar {
  width: 6px;
}

.ProjectDescription::-webkit-scrollbar-thumb {
  background-color: var(--accent-color);
  border-radius: 3px;
}
