@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500&display=swap');

:root {
  --accent-color: #8a3c4c;
  --text-color: #cecece;
  --shadow-text: 1px 1px #cecece38;
  --shadow: drop-shadow(0px 4px 4px rgb(0, 0, 0));
  --label-size: clamp(0.9rem, 0.6vw + 0.6rem, 1.2rem);
  --input-size: clamp(0.8rem, 0.5vw + 0.5rem, 1.1rem);
  --button-size: clamp(1rem, 0.8vw + 0.6rem, 1.4rem);
  --textarea-height: clamp(2.5rem, 6dvh, 4rem);
  --message-height: clamp(8rem, 18dvh, 14rem);
}

form {
  width: 100%;
}

.ContactContainer {
  width: 100%;
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
}

.ContactContainer label {
  margin: 0;
  padding: 0;
  font-size: var(--label-size);
  color: var(--text-color);
  font-family: 'Poppins', sans-serif;
}

.ContactContainer textarea {
  width: 100%;
  height: var(--textarea-height);
  background-color: var(--accent-color);
  padding: 0.6rem 0.8rem;
  font-size: var(--input-size);
  color: var(--text-color);
  resize: none;
  border: none;
  border-radius: 0.4rem;
  font-family: 'Poppins', sans-serif;
}

.MessageInput {
  height: var(--message-height) !important;
}

.SendButton {
  display: block;
  width: clamp(10rem, 15vw, 15rem);
  height: clamp(3rem, 4.5dvh, 4rem);
  background-color: var(--accent-color);
  border-radius: 40px;
  margin: 2.5rem auto 0;
  font-size: var(--button-size);
  color: var(--text-color);
  text-shadow: var(--shadow-text);
  font-family: 'Poppins', sans-serif;
  border: none;
  transition: transform 0.2s ease, background-color 0.2s ease;
}

.SendButton:hover {
  transform: scale(1.05);
  cursor: pointer;
  background-color: #813645;
}
