:root {
  --accent-color: #8a3c4c;
  --text-color: #cecece;
  --bg-color: #131210;
  --shadow: drop-shadow(0px 1px 1px rgb(0, 0, 0));
  --font-size-p: clamp(0.7rem, 0.6vw + 0.4rem, 1rem);
  --bar-height: clamp(2.5rem, 4dvh, 4rem);
  --icon-size: clamp(1.6rem, 3.5dvh, 2.5rem);
  --gap-size: clamp(0.6rem, 1vw, 1.2rem);
}

p {
  color: var(--text-color);
  font-size: var(--font-size-p);
  font-family: 'Poppins', sans-serif;
}

.Container {
  position: fixed;
  bottom: 0;
  width: 100%;
  height: var(--bar-height);
  z-index: 10;
}

.DarkLine {
  position: fixed;
  bottom: 0;
  width: 100%;
  height: var(--bar-height);
  display: flex;
  justify-content: flex-start;
  align-items: center;
  padding-left: clamp(0.5rem, 1vw, 1.5rem);
  background-color: var(--bg-color);
  z-index: 8;
}

.IconClass {
  position: fixed;
  right: clamp(10px, 1.5vw, 15px);
  bottom: clamp(10px, 1.5dvh, 15px);
  display: flex;
  flex-direction: row;
  align-items: center;
  gap: var(--gap-size);
}

.Icon {
  height: var(--icon-size);
  filter: var(--shadow);
  transition: transform 0.2s ease;
}

a:hover .Icon {
  transform: scale(1.05);
  cursor: pointer;
}
