@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500&display=swap');

:root {
  --accent-color: #8a3c4c;
  --text-color: #cecece;
  --shadow: drop-shadow(0px 4px 4px rgb(0, 0, 0));
  --line-height: 1px;
  --circle-size: clamp(0.8rem, 1dvh + 0.2rem, 1.2rem);
  --font-size-skill: clamp(0.9rem, 0.8vw + 0.5rem, 1.3rem);
  --gap-size: clamp(6px, 0.8vw, 12px);
}

.AdditionalSkillContainer {
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  margin-bottom: clamp(0.4rem, 0.8dvh, 1rem);
}

.SkillInfo {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  flex-direction: row;
  white-space: nowrap;
  width: 100%;
}

.Line {
  width: 7%;
  margin: 0 clamp(0.3rem, 0.5vw, 0.7rem) 0 0;
  height: var(--line-height);
  background-color: var(--accent-color);
  filter: var(--shadow);
}

.AdditionalSkillLabel {
  font-size: var(--font-size-skill);
  color: var(--text-color);
  margin: 0;
  font-family: 'Poppins', sans-serif;
}

.AdditionalSkillLevel {
  display: flex;
  flex-direction: row;
  gap: var(--gap-size);
}

.Circle {
  width: var(--circle-size);
  height: var(--circle-size);
  border-radius: 50%;
  border: 2px solid var(--accent-color);
  filter: var(--shadow);
}

.FilledCircle {
  background-color: var(--accent-color);
}
