:root {
  --bg-color: #131210;
  --accent-color: #8a3c4c;
  --accent-hover: #cc586f;
  --line-color: #b65b6e;
  --text-light: #cecece;
  --menu-height: clamp(3.5rem, 10dvh, 6rem);
  --font-size-menu: clamp(1.2rem, 1.5vw + 0.6rem, 2.2rem);
  --gap-menu: clamp(1rem, 2vw, 2.5rem);
}

.Line {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: var(--menu-height);
  background: var(--bg-color);
  z-index: 7;
  pointer-events: none; /* nie blokuje hovera */
}

.Menu {
  list-style: none;
  position: fixed;
  top: 0;
  right: clamp(0.8rem, 2%, 2rem);
  height: var(--menu-height);
  display: flex;
  gap: var(--gap-menu);
  align-items: center;
  z-index: 10;
  overflow: visible;
}

.Menu li {
  position: relative;
  transition: transform 0.2s ease;
}

.Menu a {
  position: relative;
  display: inline-block;
  text-decoration: none;
  color: var(--accent-color);
  font-family: 'Poppins', sans-serif;
  font-size: var(--font-size-menu);
  font-weight: 500;
  transition: color 0.3s ease, transform 0.2s ease;
}

.Menu a::after {
  content: "";
  position: absolute;
  left: 0;
  bottom: -4px;
  width: 100%;
  height: 2px;
  background: var(--line-color);
  transform: scaleX(0);
  transform-origin: left;
  transition: transform 0.4s ease;
}

.Menu li:hover {
  transform: scale(1.05);
}

.Menu a:hover {
  color: var(--accent-hover);
}

.Menu a:hover::after {
  transform: scaleX(1);
}

/* üîπ Ikona mobilnego menu */
.MobileMenuIcon {
  position: fixed;
  top: clamp(10px, 1.5dvh, 15px);
  right: clamp(10px, 1.5vw, 15px);
  z-index: 14;
  color: var(--text-light);
  visibility: hidden;
  transition: transform 0.2s ease;
}

.MobileMenuIcon:hover {
  transform: scale(1.05);
  cursor: pointer;
}

/* üîπ Menu mobilne */
.MobileMenu {
  position: fixed;
  display: flex;
  flex-direction: column;
  gap: 1rem;
  top: 0;
  width: 100%;
  padding-top: clamp(4rem, 10dvh, 6rem);
  padding-bottom: clamp(1rem, 2dvh, 2rem);
  background-color: var(--accent-color);
  z-index: 11;
  visibility: hidden;
}

.Visible {
  visibility: visible;
}

.NotVisible {
  visibility: hidden;
}

.MenuButtonMobile {
  font-family: 'Poppins', sans-serif;
  filter: drop-shadow(0px 2px 2px rgb(0, 0, 0));
  font-weight: 500;
  color: var(--text-light);
  text-decoration: none;
  font-size: clamp(1.2rem, 2vw + 0.5rem, 1.8rem);
}

.MobileMenuHr {
  width: 100%;
  border: none;
  border-bottom: 1px solid rgba(255, 255, 255, 0.2);
}

.MenuButtons {
  display: none;
}

/* üîπ Breakpointy */
@media (max-width: 1500px) {
  .Line {
    height: clamp(4rem, 14dvh, 7rem);
  }
}

@media (max-width: 1000px) {
  .Menu {
    visibility: hidden;
  }

  .MenuButtons {
    display: flex;
  }

  .MobileMenuIcon {
    visibility: visible;
  }

  .Line {
    height: clamp(4.5rem, 17dvh, 8rem);
  }
}
