:root {
  --hero-font-size: clamp(1.6rem, 2.2vw + 1rem, 3.5rem);
  --text-color: #ececec;
  --accent-color: #E8AEBB;
  --shadow: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.4));
}

.HeroSection {
  position: fixed;
  display: flex;
  flex-direction: column;
  top: clamp(0.8rem, 1.5dvh, 1.2rem);
  left: clamp(1rem, 2vw, 2.5rem);
  z-index: 10;
  font-size: var(--hero-font-size);
  filter: var(--shadow);
  font-family: 'Poppins', sans-serif;
  line-height: 1.1;
}

.NameText {
  color: var(--text-color);
}

.RoleText {
  color: var(--accent-color);
}

@media (max-width: 1000px) {
  .HeroSection {
    left: clamp(0.5rem, 1vw, 1rem);
    top: clamp(0.5rem, 1dvh, 0.8rem);
    font-size: clamp(1.4rem, 3.5vw + 0.5rem, 2.4rem);
  }
}
